.App {
    font-family: 'Raleway', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;


    // background-image: "/public/images/12394150-ilustracion-vectorial-de-tres-mujeres-jovenes-con-sus-bolsas-de-compras-de-cada-mujer-se-agrupan-y-s.jpg";
    // background-size: cover;
    // background-repeat: no-repeat;

    #logo-title{
      background-color: #af224d;
    }
    div .logo{
      width: 100vw;
      
    }
    img{
        display: flex;
        align-items: flex-start;
        width: 4em;
    }
    header {
        background-image: "/images/stop2.png";
        background-color: #DF7B42;
        min-height: 100vh;
        display: flex;
        flex-direction: flex;
        align-items: center;
        justify-content: top;
        font-size: calc(10px + 2vmin);
        color: white;
      }
      
      h3{
        color: #af224d;
      }
      
      a {
        color: white;
        cursor: pointer;
      }
      nav{
        font-display: flex;
        justify-content: space-between;
        align-items: flex-end;
        width: 100vw;
        background-color:#af224d;
      }
       ul{
          list-style: none; 
          a{
            text-decoration: none;
            cursor: pointer;
          }
        }
      
      .nav-style{
        font-display: flex;
        justify-content: space-between;

      }
      
  }

  
//   .App-logo {
//     animation: App-logo-spin infinite 20s linear;
//     height: 40vmin;
//   }
  
  .App-link {
    color: #61dafb;
  }
  
  
  fieldset {
    border: 0;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
